import { type ClassValue, clsx } from 'clsx';
import { twMerge } from 'tailwind-merge';

export function cn(...inputs: ClassValue[]) {
  return twMerge(clsx(inputs));
}

export function formatRelativeTime(date: string): string {
  const now = new Date();
  const then = new Date(date);
  const diff = now.getTime() - then.getTime();
  
  const seconds = Math.floor(diff / 1000);
  const minutes = Math.floor(seconds / 60);
  const hours = Math.floor(minutes / 60);
  const days = Math.floor(hours / 24);
  
  if (seconds < 60) return 'just now';
  if (minutes < 60) return `${minutes}m ago`;
  if (hours < 24) return `${hours}h ago`;
  if (days < 7) return `${days}d ago`;
  return then.toLocaleDateString('en-US', { month: 'short', day: 'numeric' });
}

export function getPriorityColor(priority: string): string {
  switch (priority) {
    case 'urgent':
      return 'text-red-500 bg-red-500/20 border-red-500';
    case 'high':
      return 'text-orange-500 bg-orange-500/20 border-orange-500';
    case 'medium':
      return 'text-yellow-500 bg-yellow-500/20 border-yellow-500';
    default:
      return 'text-gray-500 bg-gray-500/20 border-gray-500';
  }
}

export function getStatusColor(status: string): string {
  switch (status) {
    case 'active':
    case 'online':
      return 'bg-green-400';
    case 'standby':
      return 'bg-yellow-400';
    case 'planned':
      return 'bg-blue-400';
    default:
      return 'bg-gray-400';
  }
}

export function generateStars(count: number = 100): Array<{ left: string; top: string; size: string; delay: string }> {
  return Array.from({ length: count }, () => ({
    left: `${Math.random() * 100}%`,
    top: `${Math.random() * 100}%`,
    size: `${Math.random() * 3 + 1}px`,
    delay: `${Math.random() * 2}s`,
  }));
}