import { useEffect, useState } from 'react';

interface TorontoTime {
  time: string;
  date: string;
  greeting: string;
  hour: number;
}

export function useTorontoTime(): TorontoTime {
  const [torontoTime, setTorontoTime] = useState<TorontoTime>(() => calculateTorontoTime());

  useEffect(() => {
    const interval = setInterval(() => {
      setTorontoTime(calculateTorontoTime());
    }, 1000);

    return () => clearInterval(interval);
  }, []);

  return torontoTime;
}

function calculateTorontoTime(): TorontoTime {
  const now = new Date();
  const toronto = new Date(now.toLocaleString('en-US', { timeZone: 'America/Toronto' }));
  
  const hour = toronto.getHours();
  let greeting = 'Morning';
  if (hour >= 12 && hour < 17) greeting = 'Afternoon';
  else if (hour >= 17) greeting = 'Evening';

  return {
    time: toronto.toLocaleTimeString('en-US', {
      hour: '2-digit',
      minute: '2-digit',
      second: '2-digit',
      hour12: true,
    }),
    date: toronto.toLocaleDateString('en-US', {
      weekday: 'short',
      month: 'short',
      day: 'numeric',
      year: 'numeric',
    }),
    greeting,
    hour,
  };
}