import { useState, useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { 
  Brain, 
  Search, 
  FileText, 
  X, 
  ChevronRight,
  Clock,
  MessageSquare,
  Zap,
  Loader2,
  RefreshCw
} from 'lucide-react';
import { useDashboardStore } from '../store';
import type { MemoryFile } from '../types';
import { cn } from '../utils';

const categoryColors = {
  user: { 
    color: 'text-blue-500', 
    bgColor: 'bg-blue-500/10',
    borderColor: 'border-blue-500/30',
    label: 'Profile'
  },
  memory: { 
    color: 'text-violet-500', 
    bgColor: 'bg-violet-500/10',
    borderColor: 'border-violet-500/30',
    label: 'Memory'
  },
  daily: { 
    color: 'text-emerald-500', 
    bgColor: 'bg-emerald-500/10',
    borderColor: 'border-emerald-500/30',
    label: 'Daily'
  },
  project: { 
    color: 'text-amber-500', 
    bgColor: 'bg-amber-500/10',
    borderColor: 'border-amber-500/30',
    label: 'Project'
  },
};

function MemoryFileCard({ 
  file, 
  isSelected, 
  onClick 
}: { 
  file: MemoryFile; 
  isSelected: boolean; 
  onClick: () => void;
}) {
  const config = categoryColors[file.category];

  return (
    <motion.div
      layout
      initial={{ opacity: 0, x: -20 }}
      animate={{ opacity: 1, x: 0 }}
      whileHover={{ scale: 1.02, x: 4 }}
      whileTap={{ scale: 0.98 }}
      onClick={onClick}
      className={cn(
        'p-3 rounded-xl cursor-pointer transition-all duration-200',
        'border',
        isSelected
          ? 'bg-violet-500/10 border-violet-500/40 shadow-lg shadow-violet-500/10'
          : 'bg-slate-50/50 dark:bg-slate-800/30 border-slate-200 dark:border-slate-700 hover:bg-slate-100 dark:hover:bg-slate-700/50'
      )}
    >
      <div className="flex items-center gap-3">
        <div className={cn(
          'w-10 h-10 rounded-lg flex items-center justify-center flex-shrink-0',
          config.bgColor
        )}>
          <FileText className={cn('w-5 h-5', config.color)} />
        </div>
        
        <div className="flex-1 min-w-0">
          <p className={cn(
            'text-sm font-medium truncate',
            isSelected ? 'text-violet-700 dark:text-violet-300' : 'text-slate-900 dark:text-white'
          )}>
            {file.name}
          </p>
          <p className="text-xs text-slate-500 dark:text-slate-400 truncate">
            {file.preview}
          </p>
        </div>

        {isSelected && (
          <motion.div
            initial={{ opacity: 0, scale: 0.5 }}
            animate={{ opacity: 1, scale: 1 }}
          >
            <ChevronRight className="w-4 h-4 text-violet-500" />
          </motion.div>
        )}
      </div>

      {/* Category tag */}
      <div className="flex items-center gap-2 mt-2">
        <span className={cn(
          'text-[10px] px-2 py-0.5 rounded-full font-medium border',
          config.bgColor,
          config.color,
          config.borderColor
        )}>
          {config.label}
        </span>
        <span className="text-[10px] text-slate-400 flex items-center gap-1">
          <Clock className="w-3 h-3" />
          {file.lastModified}
        </span>
      </div>
    </motion.div>
  );
}

function MemoryPreview({ file }: { file: MemoryFile | null }) {
  if (!file) {
    return (
      <div className="h-full flex flex-col items-center justify-center text-slate-400 dark:text-slate-500 p-8">
        <motion.div
          animate={{ 
            scale: [1, 1.1, 1],
            opacity: [0.3, 0.5, 0.3]
          }}
          transition={{ duration: 3, repeat: Infinity }}
        >
          <Brain className="w-16 h-16 mb-4" />
        </motion.div>
        <p className="text-center font-medium mb-1">Select a memory</p>
        <p className="text-center text-sm opacity-70">View file contents and details</p>
      </div>
    );
  }

  const config = categoryColors[file.category];

  return (
    <motion.div
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      className="h-full flex flex-col"
    >
      {/* Header */}
      <div className="flex items-center justify-between mb-4 pb-4 border-b border-slate-200 dark:border-slate-700">
        <div className="flex items-center gap-3">
          <div className={cn(
            'w-10 h-10 rounded-xl flex items-center justify-center',
            config.bgColor
          )}>
            <FileText className={cn('w-5 h-5', config.color)} />
          </div>
          <div>
            <h3 className="text-lg font-bold text-slate-900 dark:text-white">{file.name}</h3>
            <div className="flex items-center gap-2 text-xs text-slate-500">
              <span className={cn(
                'px-2 py-0.5 rounded-full font-medium border',
                config.bgColor,
                config.color,
                config.borderColor
              )}>
                {config.label}
              </span>
              <span></span>
              <span className="flex items-center gap-1">
                <Clock className="w-3 h-3" />
                {file.lastModified}
              </span>
            </div>
          </div>
        </div>
      </div>
      
      {/* Content */}
      <div className="flex-1 overflow-y-auto scrollbar-premium pr-2">
        <div className="prose prose-sm dark:prose-invert max-w-none">
          <div className="bg-slate-50 dark:bg-slate-800/50 rounded-xl p-4 border border-slate-200 dark:border-slate-700">
            {file.content ? (
              <pre className="text-sm text-slate-700 dark:text-slate-300 whitespace-pre-wrap font-mono leading-relaxed">
                {file.content}
              </pre>
            ) : (
              <p className="text-slate-500 dark:text-slate-400 italic">
                {file.preview}
              </p>
            )}
          </div>
        </div>
      </div>

      {/* Footer */}
      <div className="mt-4 pt-4 border-t border-slate-200 dark:border-slate-700 flex items-center justify-between">
        <div className="flex items-center gap-2">
          <Zap className="w-4 h-4 text-amber-500" />
          <span className="text-xs text-slate-500">AI-processed</span>
        </div>
        <motion.button
          whileHover={{ scale: 1.05 }}
          whileTap={{ scale: 0.95 }}
          className="flex items-center gap-2 px-3 py-1.5 rounded-lg bg-violet-500/10 text-violet-600 dark:text-violet-400 text-xs font-medium hover:bg-violet-500/20 transition-colors"
        >
          <MessageSquare className="w-3.5 h-3.5" />
          Ask about this
        </motion.button>
      </div>
    </motion.div>
  );
}

export function BrainPanel() {
  const { memoryFiles: storeFiles, selectedMemory, setSelectedMemory } = useDashboardStore();
  const [memoryFiles, setMemoryFiles] = useState(storeFiles);
  const [query, setQuery] = useState('');
  const [filteredItems, setFilteredItems] = useState(memoryFiles);
  const [activeCategory, setActiveCategory] = useState<string | null>(null);
  const [loading, setLoading] = useState(false);
  const [searching, setSearching] = useState(false);
  
  const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3001';
  
  const selectedFile = memoryFiles.find((f) => f.id === selectedMemory) || null;
  
  // Fetch real files on mount
  useEffect(() => {
    fetchFiles();
  }, []);
  
  // Search when query changes
  useEffect(() => {
    if (query.length >= 2) {
      performSearch(query);
    } else {
      setFilteredItems(activeCategory 
        ? memoryFiles.filter(f => f.category === activeCategory)
        : memoryFiles
      );
    }
  }, [query, memoryFiles, activeCategory]);
  
  async function fetchFiles() {
    setLoading(true);
    try {
      const res = await fetch(`${API_URL}/api/files`);
      if (res.ok) {
        const files = await res.json();
        
        // Convert to MemoryFile format
        const mappedFiles = files
          .filter((f: any) => f.name.endsWith('.md') || f.name.endsWith('.txt') || f.name.endsWith('.json'))
          .slice(0, 20)
          .map((f: any, i: number) => ({
            id: `file-${i}`,
            name: f.name,
            preview: f.type === 'directory' ? 'Directory' : `${(f.size / 1024).toFixed(1)} KB`,
            lastModified: f.lastModified.split('T')[0],
            category: f.name.includes('memory') ? 'memory' : 
                      f.name.includes('USER') ? 'user' :
                      f.name.includes('SOUL') ? 'user' :
                      f.name.includes('DASHBOARD') ? 'project' : 'project',
            path: f.path,
          }));
        
        setMemoryFiles(mappedFiles);
        setFilteredItems(mappedFiles);
      }
    } catch (error) {
      console.error('Failed to fetch files:', error);
      // Fall back to store files
      setMemoryFiles(storeFiles);
      setFilteredItems(storeFiles);
    } finally {
      setLoading(false);
    }
  }
  
  async function performSearch(searchQuery: string) {
    setSearching(true);
    try {
      const res = await fetch(`${API_URL}/api/search?q=${encodeURIComponent(searchQuery)}`);
      if (res.ok) {
        const results = await res.json();
        const searchResults = results.map((r: any, i: number) => ({
          id: `search-${i}`,
          name: r.path.split('/').pop() || r.path,
          preview: r.preview,
          lastModified: new Date().toISOString().split('T')[0],
          category: 'project' as const,
          content: r.preview,
          path: r.path,
        }));
        setFilteredItems(searchResults);
      }
    } catch (error) {
      // Fall back to local search
      const local = memoryFiles.filter(f => 
        f.name.toLowerCase().includes(searchQuery.toLowerCase()) ||
        f.preview.toLowerCase().includes(searchQuery.toLowerCase())
      );
      setFilteredItems(local);
    } finally {
      setSearching(false);
    }
  }
  
  // Load file content when selected
  async function loadFileContent(file: any) {
    if (!file.path) return;
    try {
      const res = await fetch(`${API_URL}/api/file?path=${encodeURIComponent(file.path)}`);
      if (res.ok) {
        const data = await res.json();
        // Update the file with content
        setMemoryFiles(prev => prev.map(f => 
          f.id === file.id ? { ...f, content: data.content } : f
        ));
      }
    } catch (error) {
      console.error('Failed to load file:', error);
    }
  }

  // Filter by category if selected
  const displayFiles = activeCategory 
    ? filteredItems.filter(f => f.category === activeCategory)
    : filteredItems;

  const clearSearch = () => {
    setQuery('');
    setActiveCategory(null);
    setFilteredItems(memoryFiles);
  };
  
  const handleFileClick = (file: any) => {
    setSelectedMemory(file.id);
    loadFileContent(file);
  };

  return (
    <motion.section
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.5, duration: 0.5 }}
      className="glass-panel p-4 sm:p-6 h-full"
    >
      {/* Header */}
      <div className="flex items-center justify-between gap-3 mb-5">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-violet-500/20 to-fuchsia-500/20 flex items-center justify-center">
            <Brain className="w-5 h-5 text-violet-500" />
          </div>
          <div>
            <h2 className="text-lg sm:text-xl font-bold text-slate-900 dark:text-white font-display">
              The Brain
            </h2>
            <p className="text-xs text-slate-500 dark:text-slate-400">
              {memoryFiles.length} memories indexed
            </p>
          </div>
        </div>

        <div className="flex items-center gap-2">
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={fetchFiles}
            disabled={loading}
            className="p-2 rounded-lg bg-slate-100 dark:bg-slate-800 text-slate-500 hover:text-slate-700 dark:hover:text-slate-300 transition-colors"
          >
            <RefreshCw className={cn("w-4 h-4", loading && "animate-spin")} />
          </motion.button>
          
          {/* Quick stats */}
          <div className="hidden sm:flex items-center gap-2">
            <div className="flex -space-x-2">
              {(['user', 'memory', 'daily', 'project'] as const).map((cat) => (
                <div
                  key={cat}
                  className={cn(
                    'w-7 h-7 rounded-full flex items-center justify-center text-xs font-bold border-2 border-white dark:border-slate-800',
                    categoryColors[cat].bgColor,
                    categoryColors[cat].color
                  )}
                >
                  {memoryFiles.filter(f => f.category === cat).length}
                </div>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Search */}
      <div className="relative mb-4">
        {searching ? (
          <Loader2 className="w-5 h-5 absolute left-3 top-1/2 -translate-y-1/2 text-violet-500 animate-spin" />
        ) : (
          <Search className="w-5 h-5 absolute left-3 top-1/2 -translate-y-1/2 text-slate-400" />
        )}
        <input
          type="text"
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          placeholder="Search workspace files..."
          className="w-full bg-slate-50 dark:bg-slate-800/50 border border-slate-200 dark:border-slate-700 rounded-xl py-3 pl-10 pr-24 text-slate-900 dark:text-white placeholder-slate-400 focus:outline-none focus:border-violet-500 focus:ring-2 focus:ring-violet-500/20 transition-all"
        />
        
        {/* Category filter buttons */}
        <div className="absolute right-2 top-1/2 -translate-y-1/2 flex gap-1">
          <AnimatePresence>
            {query && (
              <motion.button
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                exit={{ opacity: 0, scale: 0.8 }}
                onClick={clearSearch}
                className="p-1.5 rounded-lg hover:bg-slate-200 dark:hover:bg-slate-700 transition-colors"
              >
                <X className="w-4 h-4 text-slate-400" />
              </motion.button>
            )}
          </AnimatePresence>
        </div>
      </div>

      {/* Category filters */}
      <div className="flex flex-wrap gap-2 mb-4">
        {(['user', 'memory', 'daily', 'project'] as const).map((cat) => (
          <button
            key={cat}
            onClick={() => setActiveCategory(activeCategory === cat ? null : cat)}
            className={cn(
              'px-3 py-1.5 rounded-lg text-xs font-medium transition-all border',
              activeCategory === cat
                ? `${categoryColors[cat].bgColor} ${categoryColors[cat].color} ${categoryColors[cat].borderColor}`
                : 'bg-slate-100 dark:bg-slate-800 text-slate-600 dark:text-slate-400 border-transparent hover:bg-slate-200 dark:hover:bg-slate-700'
            )}
          >
            {categoryColors[cat].label}
            <span className="ml-1.5 opacity-60">
              {memoryFiles.filter(f => f.category === cat).length}
            </span>
          </button>
        ))}
      </div>

      {/* Results count */}
      {query && (
        <motion.p
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          className="text-xs text-slate-500 dark:text-slate-400 mb-3"
        >
          Found <span className="font-medium text-violet-600 dark:text-violet-400">{displayFiles.length}</span> result{displayFiles.length !== 1 ? 's' : ''}
        </motion.p>
      )}

      {/* Content area */}
      <div className="grid grid-cols-1 lg:grid-cols-2 gap-4 h-[350px] sm:h-[400px]">
        {/* File List */}
        <div className="bg-slate-50/50 dark:bg-slate-900/30 rounded-xl p-3 overflow-y-auto scrollbar-premium border border-slate-200 dark:border-slate-800">
          <AnimatePresence mode="popLayout">
            {loading ? (
              <div className="flex items-center justify-center py-12">
                <Loader2 className="w-6 h-6 text-violet-500 animate-spin" />
              </div>
            ) : filteredItems.length > 0 ? (
              <div className="space-y-2">
                {filteredItems.map((file) => (
                  <MemoryFileCard
                    key={file.id}
                    file={file}
                    isSelected={selectedMemory === file.id}
                    onClick={() => handleFileClick(file)}
                  />
                ))}
              </div>
            ) : (
              <motion.div
                initial={{ opacity: 0 }}
                animate={{ opacity: 1 }}
                className="text-center py-12"
              >
                <Search className="w-12 h-12 mx-auto mb-3 text-slate-300 dark:text-slate-600" />
                <p className="text-slate-500 dark:text-slate-400 text-sm">No files found</p>
                {query && (
                  <button
                    onClick={clearSearch}
                    className="mt-2 text-xs text-violet-500 hover:underline"
                  >
                    Clear search
                  </button>
                )}
              </motion.div>
            )}
          </AnimatePresence>
        </div>

        {/* Preview Pane - Hidden on mobile, shown on lg+ */}
        <div className="hidden lg:block bg-slate-50/50 dark:bg-slate-900/30 rounded-xl p-4 overflow-hidden border border-slate-200 dark:border-slate-800">
          <MemoryPreview file={selectedFile} />
        </div>
      </div>
    </motion.section>
  );
}
