import { useMemo } from 'react';
import { motion } from 'framer-motion';
import { useDashboardStore } from '../store';
import { generateStars } from '../utils';

export function StarBackground() {
  const { theme } = useDashboardStore();
  
  // Generate stars once to prevent hydration mismatch
  const stars = useMemo(() => generateStars(80), []);
  const shootingStars = useMemo(() => [
    { delay: 0, duration: 3, top: '20%', left: '10%' },
    { delay: 5, duration: 4, top: '40%', left: '80%' },
    { delay: 8, duration: 3.5, top: '60%', left: '30%' },
    { delay: 12, duration: 3, top: '15%', left: '70%' },
  ], []);

  if (theme === 'light') {
    return (
      <div className="fixed inset-0 pointer-events-none z-0 overflow-hidden">
        {/* Soft gradient background */}
        <div className="absolute inset-0 bg-gradient-to-br from-slate-50 via-blue-50/20 to-purple-50/10" />
        
        {/* Subtle particles */}
        {stars.map((star, i) => (
          <motion.div
            key={`light-${i}`}
            initial={{ opacity: 0 }}
            animate={{ 
              opacity: [0.05, 0.15, 0.05],
              scale: [1, 1.2, 1]
            }}
            transition={{
              duration: 3 + (i % 5) * 0.5,
              repeat: Infinity,
              delay: (i % 10) * 0.2,
              ease: 'easeInOut',
            }}
            className="absolute rounded-full bg-violet-400/30"
            style={{
              left: star.left,
              top: star.top,
              width: star.size,
              height: star.size,
            }}
          />
        ))}

        {/* Soft nebula effects for light mode */}
        <div className="absolute top-0 right-0 w-[600px] h-[600px] bg-gradient-radial from-violet-200/20 via-transparent to-transparent rounded-full blur-3xl" />
        <div className="absolute bottom-0 left-0 w-[500px] h-[500px] bg-gradient-radial from-fuchsia-200/20 via-transparent to-transparent rounded-full blur-3xl" />
      </div>
    );
  }

  return (
    <div className="fixed inset-0 pointer-events-none z-0 overflow-hidden">
      {/* Deep space gradient */}
      <div className="absolute inset-0 bg-dark-gradient" />
      
      {/* Star field */}
      {stars.map((star, i) => (
        <motion.div
          key={`dark-${i}`}
          initial={{ opacity: 0 }}
          animate={{ 
            opacity: [0.2, 0.8 + (i % 3) * 0.1, 0.2],
            scale: [1, 1.1, 1],
          }}
          transition={{
            duration: 2 + (i % 5) * 0.7,
            repeat: Infinity,
            delay: parseFloat(star.delay),
            ease: 'easeInOut',
          }}
          className="absolute rounded-full"
          style={{
            left: star.left,
            top: star.top,
            width: star.size,
            height: star.size,
            backgroundColor: i % 7 === 0 ? '#a78bfa' : i % 5 === 0 ? '#67e8f9' : '#ffffff',
            boxShadow: `0 0 ${parseFloat(star.size) * 3}px ${i % 7 === 0 ? 'rgba(167, 139, 250, 0.5)' : i % 5 === 0 ? 'rgba(103, 232, 249, 0.5)' : 'rgba(255, 255, 255, 0.5)'}`,
          }}
        />
      ))}

      {/* Shooting stars */}
      {shootingStars.map((star, i) => (
        <motion.div
          key={`shooting-${i}`}
          initial={{ x: '-100%', opacity: 0 }}
          animate={{ 
            x: '200vw',
            opacity: [0, 1, 1, 0],
          }}
          transition={{
            duration: star.duration,
            repeat: Infinity,
            delay: star.delay,
            ease: 'linear',
          }}
          className="absolute h-px w-24"
          style={{
            top: star.top,
            left: star.left,
            background: 'linear-gradient(90deg, transparent, rgba(255,255,255,0.8), transparent)',
            transform: 'rotate(-45deg)',
          }}
        />
      ))}

      {/* Nebula effects */}
      <motion.div 
        animate={{ 
          scale: [1, 1.1, 1],
          opacity: [0.3, 0.4, 0.3],
        }}
        transition={{ duration: 10, repeat: Infinity, ease: 'easeInOut' }}
        className="absolute top-1/4 left-1/4 w-[500px] h-[500px] bg-violet-600/10 rounded-full blur-3xl" 
      />
      <motion.div 
        animate={{ 
          scale: [1, 1.15, 1],
          opacity: [0.2, 0.35, 0.2],
        }}
        transition={{ duration: 12, repeat: Infinity, ease: 'easeInOut', delay: 2 }}
        className="absolute bottom-1/4 right-1/4 w-[600px] h-[600px] bg-fuchsia-600/10 rounded-full blur-3xl" 
      />
      <motion.div 
        animate={{ 
          scale: [1, 1.2, 1],
          opacity: [0.2, 0.3, 0.2],
        }}
        transition={{ duration: 15, repeat: Infinity, ease: 'easeInOut', delay: 5 }}
        className="absolute top-1/2 left-1/2 w-[400px] h-[400px] bg-cyan-600/10 rounded-full blur-3xl -translate-x-1/2 -translate-y-1/2" 
      />

      {/* Cosmic dust */}
      <div className="absolute inset-0 opacity-30" style={{
        backgroundImage: `url("data:image/svg+xml,%3Csvg viewBox='0 0 400 400' xmlns='http://www.w3.org/2000/svg'%3E%3Cfilter id='noiseFilter'%3E%3CfeTurbulence type='fractalNoise' baseFrequency='0.9' numOctaves='3' stitchTiles='stitch'/%3E%3C/filter%3E%3Crect width='100%25' height='100%25' filter='url(%23noiseFilter)'/%3E%3C/svg%3E")`,
        mixBlendMode: 'overlay',
      }} />
    </div>
  );
}
