import { motion } from 'framer-motion';
import { Users, Bot, Activity, Sparkles, Zap, Shield, Target, Cpu } from 'lucide-react';
import { useDashboardStore } from '../store';
import type { Agent, AgentStatus, Team } from '../types';
import { cn } from '../utils';

const statusConfig: Record<AgentStatus, { 
  label: string; 
  color: string;
  bgColor: string;
  pulse: boolean;
}> = {
  active: { 
    label: 'Online', 
    color: 'text-emerald-500',
    bgColor: 'bg-emerald-500',
    pulse: true,
  },
  standby: { 
    label: 'Standby', 
    color: 'text-amber-500',
    bgColor: 'bg-amber-500',
    pulse: false,
  },
  offline: { 
    label: 'Offline', 
    color: 'text-slate-400',
    bgColor: 'bg-slate-400',
    pulse: false,
  },
  planned: { 
    label: 'Planned', 
    color: 'text-blue-500',
    bgColor: 'bg-blue-500',
    pulse: false,
  },
};

const teamIcons: Record<string, React.ElementType> = {
  marketing: Target,
  product: Zap,
  research: Cpu,
  utility: Shield,
};

const teamColors: Record<string, { from: string; to: string; icon: string }> = {
  marketing: { from: 'from-pink-500', to: 'to-rose-500', icon: 'text-pink-500' },
  product: { from: 'from-violet-500', to: 'to-purple-500', icon: 'text-violet-500' },
  research: { from: 'from-cyan-500', to: 'to-blue-500', icon: 'text-cyan-500' },
  utility: { from: 'from-amber-500', to: 'to-orange-500', icon: 'text-amber-500' },
};

function AgentCard({ agent, teamId }: { agent: Agent; teamId: string }) {
  const status = statusConfig[agent.status];
  const colors = teamColors[teamId];

  return (
    <motion.div
      whileHover={{ scale: 1.02, x: 4 }}
      whileTap={{ scale: 0.98 }}
      className="flex items-center gap-3 p-3 rounded-xl bg-white/50 dark:bg-slate-800/50 border border-slate-200 dark:border-slate-700/50 hover:border-slate-300 dark:hover:border-slate-600 transition-all cursor-pointer group"
    >
      {/* Status indicator */}
      <div className="relative">
        <motion.div
          animate={status.pulse ? {
            boxShadow: ['0 0 0 0 rgba(52, 211, 153, 0.4)', '0 0 0 6px rgba(52, 211, 153, 0)', '0 0 0 0 rgba(52, 211, 153, 0)']
          } : {}}
          transition={{ duration: 2, repeat: Infinity }}
          className={cn(
            'w-2.5 h-2.5 rounded-full',
            status.bgColor
          )}
        />
      </div>

      {/* Avatar */}
      <div className={cn(
        'w-9 h-9 rounded-lg flex items-center justify-center text-sm font-bold',
        'bg-gradient-to-br',
        colors.from,
        colors.to,
        'text-white shadow-sm'
      )}>
        {agent.name.split(' ').map(n => n[0]).join('')}
      </div>

      {/* Info */}
      <div className="flex-1 min-w-0">
        <span className="text-slate-900 dark:text-white font-medium text-sm block truncate">
          {agent.name}
        </span>
        <span className="text-slate-500 dark:text-slate-400 text-xs block truncate">
          {agent.role}
        </span>
      </div>

      {/* Status label on hover */}
      <span className={cn(
        'text-[10px] px-2 py-1 rounded-full font-medium opacity-0 group-hover:opacity-100 transition-opacity',
        status.bgColor + '/10',
        status.color
      )}>
        {status.label}
      </span>
    </motion.div>
  );
}

function TeamCard({ team, index }: { team: Team; index: number }) {
  const activeAgents = team.agents.filter(a => a.status === 'active').length;
  const totalAgents = team.agents.length;
  const colors = teamColors[team.id];
  const TeamIcon = teamIcons[team.id] || Bot;

  return (
    <motion.div
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.6 + index * 0.1 }}
      whileHover={{ y: -4 }}
      className="rounded-2xl overflow-hidden border border-slate-200 dark:border-slate-700/50 bg-white/40 dark:bg-slate-900/40 backdrop-blur-sm transition-all hover:shadow-xl hover:shadow-violet-500/5"
    >
      {/* Header */}
      <div className={cn(
        'p-4 bg-gradient-to-r',
        colors.from,
        colors.to,
        'text-white'
      )}>
        <div className="flex items-center justify-between">
          <div className="flex items-center gap-3">
            <div className="w-10 h-10 rounded-xl bg-white/20 backdrop-blur-sm flex items-center justify-center">
              <TeamIcon className="w-5 h-5" />
            </div>
            <div>
              <h3 className="font-bold text-lg">{team.name}</h3>
              <div className="flex items-center gap-2 text-xs text-white/80">
                <Activity className="w-3 h-3" />
                <span>{activeAgents}/{totalAgents} active</span>
              </div>
            </div>
          </div>
          
          {/* Icon */}
          <motion.span
            whileHover={{ scale: 1.2, rotate: 10 }}
            className="text-2xl opacity-80"
          >
            {team.icon}
          </motion.span>
        </div>

        {/* Progress bar */}
        <div className="mt-3 h-1.5 bg-black/20 rounded-full overflow-hidden">
          <motion.div
            initial={{ width: 0 }}
            animate={{ width: `${(activeAgents / totalAgents) * 100}%` }}
            transition={{ delay: 0.8 + index * 0.1, duration: 0.5 }}
            className="h-full bg-white/60 rounded-full"
          />
        </div>
      </div>

      {/* Agents list */}
      <div className="p-3 space-y-2">
        {team.agents.map((agent) => (
          <AgentCard key={agent.id} agent={agent} teamId={team.id} />
        ))}
      </div>
    </motion.div>
  );
}

export function AgentOrgChart() {
  const { teams } = useDashboardStore();
  
  const totalActive = teams.reduce((acc, team) => 
    acc + team.agents.filter(a => a.status === 'active').length, 0
  );
  const totalAgents = teams.reduce((acc, team) => acc + team.agents.length, 0);
  const totalStandby = teams.reduce((acc, team) => 
    acc + team.agents.filter(a => a.status === 'standby').length, 0
  );

  return (
    <motion.section
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.6, duration: 0.5 }}
      className="glass-panel p-4 sm:p-6"
    >
      {/* Header */}
      <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 mb-6">
        <div className="flex items-center gap-3">
          <div className="w-12 h-12 rounded-xl bg-gradient-to-br from-violet-500/20 to-fuchsia-500/20 flex items-center justify-center">
            <Users className="w-6 h-6 text-violet-500" />
          </div>
          <div>
            <h2 className="text-lg sm:text-xl font-bold text-slate-900 dark:text-white font-display">
              Your AI Team
            </h2>
            <p className="text-xs text-slate-500 dark:text-slate-400">
              {totalAgents} specialized agents across {teams.length} teams
            </p>
          </div>
        </div>

        {/* Stats */}
        <div className="flex items-center gap-4">
          <div className="flex items-center gap-3 bg-slate-50 dark:bg-slate-800/50 rounded-xl px-4 py-2 border border-slate-200 dark:border-slate-700">
            <div className="text-center">
              <div className="flex items-center justify-center gap-1">
                <Bot className="w-4 h-4 text-violet-500" />
                <span className="text-lg font-bold text-slate-900 dark:text-white">{totalActive}</span>
              </div>
              <span className="text-[10px] text-slate-500 uppercase">Online</span>
            </div>
            <div className="w-px h-8 bg-slate-200 dark:bg-slate-700" />
            <div className="text-center">
              <div className="text-lg font-bold text-amber-500">{totalStandby}</div>
              <span className="text-[10px] text-slate-500 uppercase">Standby</span>
            </div>
          </div>

          {/* Team avatars */}
          <div className="hidden sm:flex -space-x-2">
            {teams.map((team, i) => (
              <motion.div
                key={team.id}
                initial={{ scale: 0 }}
                animate={{ scale: 1 }}
                transition={{ delay: 0.8 + i * 0.1 }}
                className={cn(
                  'w-10 h-10 rounded-xl flex items-center justify-center text-lg border-2 border-white dark:border-slate-800 shadow-sm',
                  'bg-gradient-to-br',
                  teamColors[team.id].from,
                  teamColors[team.id].to
                )}
              >
                {team.icon}
              </motion.div>
            ))}
          </div>
        </div>
      </div>

      {/* Team grid */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-4">
        {teams.map((team, index) => (
          <TeamCard key={team.id} team={team} index={index} />
        ))}
      </div>

      {/* Footer note */}
      <div className="mt-6 flex items-center justify-center gap-2 text-xs text-slate-500 dark:text-slate-400">
        <Sparkles className="w-3.5 h-3.5 text-amber-500" />
        <span>Agents are spawned automatically when you approve decisions</span>
      </div>
    </motion.section>
  );
}
