import { useEffect } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { X, CheckCircle2, AlertTriangle, Info, AlertCircle } from 'lucide-react';
import { useDashboardStore } from '../store';
import type { Notification } from '../types';
import { cn } from '../utils';

const icons = {
  success: CheckCircle2,
  warning: AlertTriangle,
  error: AlertCircle,
  info: Info,
};

const styles = {
  success: {
    bg: 'bg-emerald-50 dark:bg-emerald-900/20',
    border: 'border-emerald-200 dark:border-emerald-800/50',
    text: 'text-emerald-800 dark:text-emerald-300',
    icon: 'text-emerald-500',
  },
  warning: {
    bg: 'bg-amber-50 dark:bg-amber-900/20',
    border: 'border-amber-200 dark:border-amber-800/50',
    text: 'text-amber-800 dark:text-amber-300',
    icon: 'text-amber-500',
  },
  error: {
    bg: 'bg-rose-50 dark:bg-rose-900/20',
    border: 'border-rose-200 dark:border-rose-800/50',
    text: 'text-rose-800 dark:text-rose-300',
    icon: 'text-rose-500',
  },
  info: {
    bg: 'bg-blue-50 dark:bg-blue-900/20',
    border: 'border-blue-200 dark:border-blue-800/50',
    text: 'text-blue-800 dark:text-blue-300',
    icon: 'text-blue-500',
  },
};

function NotificationItem({ notification, onDismiss }: { 
  notification: Notification; 
  onDismiss: () => void;
}) {
  const Icon = icons[notification.type];
  const style = styles[notification.type];

  useEffect(() => {
    const timer = setTimeout(() => {
      onDismiss();
    }, 5000);

    return () => clearTimeout(timer);
  }, [onDismiss]);

  return (
    <motion.div
      layout
      initial={{ opacity: 0, x: 100, scale: 0.9 }}
      animate={{ opacity: 1, x: 0, scale: 1 }}
      exit={{ opacity: 0, x: 100, scale: 0.9 }}
      className={cn(
        'p-4 rounded-xl border shadow-xl backdrop-blur-xl',
        'min-w-[280px] sm:min-w-[320px] max-w-md',
        style.bg,
        style.border
      )}
    >
      <div className="flex items-start gap-3">
        <div className={cn(
          'w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0',
          'bg-white/50 dark:bg-white/10',
          style.icon
        )}>
          <Icon className="w-4 h-4" />
        </div>
        <div className="flex-1 min-w-0">
          <p className={cn('font-semibold text-sm', style.text)}>
            {notification.title}
          </p>
          <p className="text-sm text-slate-600 dark:text-slate-400 mt-0.5">
            {notification.message}
          </p>
          <p className="text-[10px] text-slate-400 mt-2">
            {new Date(notification.timestamp).toLocaleTimeString('en-US', {
              hour: '2-digit',
              minute: '2-digit',
            })}
          </p>
        </div>
        <motion.button
          whileHover={{ scale: 1.1 }}
          whileTap={{ scale: 0.9 }}
          onClick={onDismiss}
          className="p-1.5 rounded-lg hover:bg-black/5 dark:hover:bg-white/10 transition-colors"
        >
          <X className="w-4 h-4 text-slate-400" />
        </motion.button>
      </div>
    </motion.div>
  );
}

export function NotificationCenter() {
  const { notifications, dismissNotification } = useDashboardStore();

  if (notifications.length === 0) return null;

  return (
    <div className="fixed top-20 right-2 sm:right-4 z-50 flex flex-col gap-2 pointer-events-none max-w-[calc(100vw-1rem)]">
      <AnimatePresence mode="popLayout">
        {notifications.map((notification) => (
          <div key={notification.id} className="pointer-events-auto">
            <NotificationItem
              notification={notification}
              onDismiss={() => dismissNotification(notification.id)}
            />
          </div>
        ))}
      </AnimatePresence>
    </div>
  );
}
