function formatSchedule(cron: string): string {
  const schedules: Record<string, string> = {
    '0 0 * * *': 'Daily at midnight',
    '0 2 * * *': 'Daily at 2 AM',
    '0 */6 * * *': 'Every 6 hours',
    '0 * * * *': 'Hourly',
  };
  return schedules[cron] || cron;
}

function formatTimeAgo(dateStr: string): string {
  const date = new Date(dateStr);
  const now = new Date();
  const diff = now.getTime() - date.getTime();
  const hours = Math.floor(diff / (1000 * 60 * 60));
  
  if (hours < 1) return 'Just now';
  if (hours < 24) return `${hours}h ago`;
  return `${Math.floor(hours / 24)}d ago`;
}

function CronJobCard({ job }: { job: CronJob }) {
  const statusConfig = {
    active: { icon: Play, color: 'text-emerald-500', bg: 'bg-emerald-500/10', border: 'border-emerald-500/20' },
    paused: { icon: Pause, color: 'text-amber-500', bg: 'bg-amber-500/10', border: 'border-amber-500/20' },
    failed: { icon: AlertTriangle, color: 'text-rose-500', bg: 'bg-rose-500/10', border: 'border-rose-500/20' },
  };

  const config = statusConfig[job.status];
  const StatusIcon = config.icon;

  return (
    <motion.div
      layout
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ scale: 1.01 }}
      className={cn(
        'relative rounded-xl p-3 border transition-all cursor-pointer',
        'bg-slate-50/50 dark:bg-slate-800/30',
        'border-slate-200 dark:border-slate-700',
        'hover:border-slate-300 dark:hover:border-slate-600',
        'hover:shadow-md'
      )}
    >
      <div className="flex items-start gap-3">
        <div className={cn('w-8 h-8 rounded-lg flex items-center justify-center', config.bg, config.border, config.color)}>
          <StatusIcon className="w-4 h-4" />
        </div>
        
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2">
            <h4 className="font-medium text-slate-900 dark:text-white text-sm truncate">
              {job.name}
            </h4>
          </div>
          <p className="text-xs text-slate-500 dark:text-slate-400 mt-0.5">
            {job.description}
          </p>
          
          <div className="flex items-center gap-3 mt-2 text-[10px] text-slate-400">
            <span className="flex items-center gap-1">
              <Clock className="w-3 h-3" />
              {formatSchedule(job.schedule)}
            </span>
            <span className="flex items-center gap-1">
              <RefreshCw className="w-3 h-3" />
              {formatTimeAgo(job.lastRun)}
            </span>
          </div>
        </div>
        
        <div className="text-[10px] text-slate-400 text-right">
          <span className="px-2 py-1 rounded-full bg-slate-100 dark:bg-slate-700">
            {job.agent}
          </span>
        </div>
      </div>
    </motion.div>
  );
}

function ProjectCard({ project }: { project: Project }) {
  const typeIcon = {
    github: GitBranch,
    local: FolderGit2,
    notion: FileText,
  };

  const TypeIcon = typeIcon[project.type];

  const getDeadlineColor = () => {
    if (!project.deadline) return '';
    const days = Math.ceil((new Date(project.deadline).getTime() - Date.now()) / (1000 * 60 * 60 * 24));
    if (days < 3) return 'text-rose-500';
    if (days < 7) return 'text-amber-500';
    return 'text-emerald-500';
  };

  return (
    <motion.div
      layout
      initial={{ opacity: 0, y: 10 }}
      animate={{ opacity: 1, y: 0 }}
      whileHover={{ scale: 1.01 }}
      className={cn(
        'relative rounded-xl p-3 border transition-all cursor-pointer group',
        'bg-slate-50/50 dark:bg-slate-800/30',
        'border-slate-200 dark:border-slate-700',
        'hover:border-slate-300 dark:hover:border-slate-600',
        'hover:shadow-md'
      )}
    >
      <div className="flex items-start gap-3">
        <div className={cn(
          'w-8 h-8 rounded-lg flex items-center justify-center',
          project.type === 'github' ? 'bg-violet-500/10 text-violet-500' :
          project.type === 'notion' ? 'bg-blue-500/10 text-blue-500' :
          'bg-emerald-500/10 text-emerald-500'
        )}>
          <TypeIcon className="w-4 h-4" />
        </div>
        
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2">
            <h4 className="font-medium text-slate-900 dark:text-white text-sm truncate">
              {project.name}
            </h4>
            <ChevronRight className="w-3 h-3 text-slate-400 opacity-0 group-hover:opacity-100 transition-opacity" />
          </div>
          <p className="text-[10px] text-slate-400 font-mono mt-0.5">
            {project.path}
          </p>
          
          <div className="flex items-center gap-3 mt-2 text-[10px]">
            <span className="text-slate-400">
              Modified {formatTimeAgo(project.lastModified + 'T00:00:00Z')}
            </span>
            {project.deadline && (
              <span className={cn('flex items-center gap-1', getDeadlineColor())}>
                <Calendar className="w-3 h-3" />
                Due {new Date(project.deadline).toLocaleDateString('en-US', { month: 'short', day: 'numeric' })}
              </span>
            )}
          </div>
        </div>
        
        <div className={cn(
          'w-2 h-2 rounded-full',
          project.status === 'active' ? 'bg-emerald-500' :
          project.status === 'draft' ? 'bg-amber-500' :
          'bg-slate-400'
        )} />
      </div>
    </motion.div>
  );
}

export function AutomationsPanel({ cronJobs: initialCronJobs, projects: initialProjects }: AutomationsPanelProps) {
  const [activeTab, setActiveTab] = useState<'cron' | 'projects'>('cron');
  const [cronJobs, setCronJobs] = useState<CronJob[]>(initialCronJobs || defaultCronJobs);
  const [projects, setProjects] = useState<Project[]>(initialProjects || defaultProjects);
  const [files, setFiles] = useState<any[]>([]);
  const [loading, setLoading] = useState(false);
  
  const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3001';
  
  // Fetch real data on mount
  useEffect(() => {
    fetchData();
  }, []);
  
  async function fetchData() {
    setLoading(true);
    try {
      // Fetch cron jobs
      const cronRes = await fetch(`${API_URL}/api/cron`);
      if (cronRes.ok) {
        const cronData = await cronRes.json();
        setCronJobs(cronData);
      }
      
      // Fetch file tree
      const filesRes = await fetch(`${API_URL}/api/files`);
      if (filesRes.ok) {
        const filesData = await filesRes.json();
        setFiles(filesData);
        
        // Convert files to projects
        const projectItems: Project[] = filesData
          .filter((f: any) => f.type === 'directory')
          .slice(0, 10)
          .map((f: any) => ({
            id: `proj-${f.name}`,
            name: f.name,
            path: f.path,
            type: f.name.includes('github') ? 'github' : 'local',
            lastModified: f.lastModified,
            status: 'active',
          }));
        
        if (!initialProjects) {
          setProjects(projectItems);
        }
      }
    } catch (error) {
      console.error('Failed to fetch automations data:', error);
    } finally {
      setLoading(false);
    }
  }
  
  const activeCronJobs = cronJobs.filter(j => j.status === 'active');
  const activeProjects = projects.filter(p => p.status === 'active');
  const urgentProjects = projects.filter(p => p.deadline && new Date(p.deadline).getTime() - Date.now() < 7 * 24 * 60 * 60 * 1000);

  return (
    <motion.section
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.5, duration: 0.5 }}
      className="glass-panel p-4 sm:p-6"
    >
      {/* Header */}
      <div className="flex items-center justify-between gap-3 mb-5">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-cyan-500/20 to-blue-500/20 flex items-center justify-center">
            <Terminal className="w-5 h-5 text-cyan-500" />
          </div>
          <div>
            <h2 className="text-lg sm:text-xl font-bold text-slate-900 dark:text-white font-display">
              Automations
            </h2>
            <p className="text-xs text-slate-500 dark:text-slate-400">
              {activeCronJobs.length} active jobs  {activeProjects.length} projects
            </p>
          </div>
        </div>

        <div className="flex items-center gap-2">
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={fetchData}
            disabled={loading}
            className="p-2 rounded-lg bg-slate-100 dark:bg-slate-800 text-slate-500 hover:text-slate-700 dark:hover:text-slate-300 transition-colors"
          >
            <RefreshCw className={cn("w-4 h-4", loading && "animate-spin")} />
          </motion.button>
          
          {urgentProjects.length > 0 && (
            <motion.div
              initial={{ scale: 0 }}
              animate={{ scale: 1 }}
              className="flex items-center gap-1.5 px-2.5 py-1.5 rounded-full bg-amber-500/10 border border-amber-500/20"
            >
              <AlertTriangle className="w-3.5 h-3.5 text-amber-500" />
              <span className="text-xs font-medium text-amber-700 dark:text-amber-400">
                {urgentProjects.length} due soon
              </span>
            </motion.div>
          )}
        </div>
      </div>

      {/* Tabs */}
      <div className="flex gap-1 p-1 rounded-xl bg-slate-100 dark:bg-slate-800/50 mb-4">
        <button
          onClick={() => setActiveTab('cron')}
          className={cn(
            'flex items-center gap-2 px-4 py-2 rounded-lg text-xs font-medium transition-all',
            activeTab === 'cron'
              ? 'bg-white dark:bg-slate-700 text-slate-900 dark:text-white shadow-sm'
              : 'text-slate-500 hover:text-slate-700 dark:hover:text-slate-300'
          )}
        >
          <Clock className="w-3.5 h-3.5" />
          Cron Jobs
          <span className="px-1.5 py-0.5 rounded-full bg-slate-200 dark:bg-slate-600 text-[10px]">
            {cronJobs.length}
          </span>
        </button>
        <button
          onClick={() => setActiveTab('projects')}
          className={cn(
            'flex items-center gap-2 px-4 py-2 rounded-lg text-xs font-medium transition-all',
            activeTab === 'projects'
              ? 'bg-white dark:bg-slate-700 text-slate-900 dark:text-white shadow-sm'
              : 'text-slate-500 hover:text-slate-700 dark:hover:text-slate-300'
          )}
        >
          <FolderGit2 className="w-3.5 h-3.5" />
          Projects
          <span className="px-1.5 py-0.5 rounded-full bg-slate-200 dark:bg-slate-600 text-[10px]">
            {projects.length}
          </span>
        </button>
      </div>

      {/* Content */}
      <div className="space-y-2 max-h-[320px] overflow-y-auto scrollbar-premium pr-1">
        {loading ? (
          <div className="flex items-center justify-center py-12">
            <Loader2 className="w-6 h-6 text-violet-500 animate-spin" />
          </div>
        ) : activeTab === 'cron' ? (
          cronJobs.map((job) => (
            <CronJobCard key={job.id} job={job} />
          ))
        ) : (
          <>
            {/* File Tree View */}
            <div className="mb-3">
              <h4 className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-2">
                Workspace Files ({files.length} items)
              </h4>
              <div className="pl-2 border-l-2 border-slate-200 dark:border-slate-700 space-y-1">
                {files.slice(0, 8).map((file) => (
                  <div 
                    key={file.path}
                    className="flex items-center gap-2 text-xs text-slate-600 dark:text-slate-300 py-1"
                  >
                    {file.type === 'directory' ? (
                      <FolderGit2 className="w-3.5 h-3.5 text-emerald-500" />
                    ) : (
                      <FileText className="w-3.5 h-3.5 text-blue-500" />
                    )}
                    <span className="truncate">{file.name}</span>
                    {file.children && (
                      <span className="text-[10px] text-slate-400">
                        ({file.children.length})
                      </span>
                    )}
                  </div>
                ))}
                {files.length > 8 && (
                  <p className="text-[10px] text-slate-400 pl-5">
                    +{files.length - 8} more items...
                  </p>
                )}
              </div>
            </div>
            
            <div className="border-t border-slate-200 dark:border-slate-700 pt-3">
              <h4 className="text-xs font-medium text-slate-500 dark:text-slate-400 mb-2">
                Active Projects
              </h4>
              {projects.map((project) => (
                <ProjectCard key={project.id} project={project} />
              ))}
            </div>
          </>
        )}
      </div>
    </motion.section>
  );
}
