import { Sparkles, Moon, Sun, Wifi, WifiOff, Clock, Calendar, MapPin } from 'lucide-react';
import { motion } from 'framer-motion';
import { useTorontoTime } from '../hooks/useTorontoTime';
import { useDashboardStore } from '../store';
import { cn } from '../utils';

export function Header() {
  const { time, date, greeting } = useTorontoTime();
  const { toggleTheme, theme, systemStatus } = useDashboardStore();

  // Status configuration
  const getStatusConfig = () => {
    switch (systemStatus.websocket) {
      case 'connected':
        return { 
          icon: Wifi, 
          text: 'Live', 
          color: 'text-emerald-500',
          bgColor: 'bg-emerald-500/20',
          borderColor: 'border-emerald-500/30',
          pulse: true 
        };
      case 'static':
        return { 
          icon: Clock, 
          text: 'Static', 
          color: 'text-blue-500',
          bgColor: 'bg-blue-500/20',
          borderColor: 'border-blue-500/30',
          pulse: false 
        };
      case 'connecting':
        return { 
          icon: Wifi, 
          text: 'Syncing', 
          color: 'text-amber-500',
          bgColor: 'bg-amber-500/20',
          borderColor: 'border-amber-500/30',
          pulse: true 
        };
      default:
        return { 
          icon: WifiOff, 
          text: 'Offline', 
          color: 'text-slate-500',
          bgColor: 'bg-slate-500/20',
          borderColor: 'border-slate-500/30',
          pulse: false 
        };
    }
  };

  const status = getStatusConfig();
  const StatusIcon = status.icon;

  return (
    <motion.header
      initial={{ y: -100, opacity: 0 }}
      animate={{ y: 0, opacity: 1 }}
      transition={{ duration: 0.6, ease: [0.22, 1, 0.36, 1] }}
      className="sticky top-0 z-50 safe-area-pt"
    >
      {/* Glass panel header */}
      <div className="glass-panel border-b-0 rounded-b-none mx-2 sm:mx-4 mt-2">
        <div className="max-w-7xl mx-auto px-3 sm:px-6 py-3 sm:py-4">
          <div className="flex items-center justify-between gap-3">
            {/* Logo */}
            <motion.div
              whileHover={{ scale: 1.02 }}
              whileTap={{ scale: 0.98 }}
              className="flex items-center gap-2 sm:gap-3 flex-shrink-0"
            >
              <div className="relative">
                <div className="w-9 h-9 sm:w-11 sm:h-11 rounded-xl sm:rounded-2xl bg-gradient-to-br from-violet-500 via-fuchsia-500 to-cyan-500 flex items-center justify-center shadow-lg shadow-violet-500/30">
                  <Sparkles className="w-4 h-4 sm:w-5 sm:h-5 text-white" />
                </div>
                {/* Status dot on logo */}
                <motion.div
                  animate={status.pulse ? {
                    boxShadow: ['0 0 0 0 rgba(52, 211, 153, 0.4)', '0 0 0 8px rgba(52, 211, 153, 0)', '0 0 0 0 rgba(52, 211, 153, 0)']
                  } : {}}
                  transition={{ duration: 2, repeat: Infinity }}
                  className={cn(
                    'absolute -bottom-0.5 -right-0.5 w-3 h-3 rounded-full border-2 border-white dark:border-slate-900',
                    status.color.replace('text-', 'bg-')
                  )}
                />
              </div>
              <div className="hidden sm:block">
                <h1 className="text-xl font-bold font-display cosmic-text leading-tight">
                  Pleiades OS
                </h1>
                <p className="text-xs text-slate-500 dark:text-slate-400 flex items-center gap-1">
                  <MapPin className="w-3 h-3" />
                  Barrie, ON
                </p>
              </div>
              {/* Mobile: Just show icon */}
              <h1 className="sm:hidden text-lg font-bold font-display cosmic-text">
                Pleiades
              </h1>
            </motion.div>

            {/* Center: Time Display */}
            <div className="flex-1 flex justify-center">
              <div className="text-center">
                <motion.div
                  key={time}
                  initial={{ opacity: 0.5, y: -2 }}
                  animate={{ opacity: 1, y: 0 }}
                  className="text-xl sm:text-2xl lg:text-3xl font-mono font-bold text-slate-800 dark:text-slate-100 tracking-tight tabular-nums"
                >
                  {time}
                </motion.div>
                <div className="hidden sm:flex items-center justify-center gap-2 text-xs text-slate-500 dark:text-slate-400">
                  <Calendar className="w-3 h-3" />
                  <span>{date}</span>
                  <span className="text-slate-300 dark:text-slate-600"></span>
                  <span>Good {greeting}, TJ</span>
                </div>
                {/* Mobile: Just show date */}
                <div className="sm:hidden text-[10px] text-slate-500 dark:text-slate-400">
                  {date}
                </div>
              </div>
            </div>

            {/* Right: Status & Theme */}
            <div className="flex items-center gap-2 sm:gap-3 flex-shrink-0">
              {/* Status Badge */}
              <motion.div
                initial={{ opacity: 0, scale: 0.8 }}
                animate={{ opacity: 1, scale: 1 }}
                className={cn(
                  'flex items-center gap-1.5 px-2 sm:px-3 py-1.5 rounded-full text-xs font-medium border',
                  status.bgColor,
                  status.color,
                  status.borderColor
                )}
              >
                <StatusIcon className={cn('w-3 h-3', status.pulse && 'animate-pulse')} />
                <span className="hidden sm:inline">{status.text}</span>
              </motion.div>

              {/* Theme Toggle */}
              <motion.button
                whileHover={{ scale: 1.05 }}
                whileTap={{ scale: 0.95 }}
                onClick={toggleTheme}
                className="touch-target p-2.5 rounded-xl bg-slate-100 dark:bg-slate-800 text-slate-600 dark:text-slate-300 hover:bg-slate-200 dark:hover:bg-slate-700 transition-colors border border-slate-200 dark:border-slate-700"
                aria-label={`Switch to ${theme === 'dark' ? 'light' : 'dark'} mode`}
              >
                <motion.div
                  initial={false}
                  animate={{ rotate: theme === 'dark' ? 0 : 180 }}
                  transition={{ duration: 0.3 }}
                >
                  {theme === 'dark' ? (
                    <Sun className="w-4 h-4 sm:w-5 sm:h-5" />
                  ) : (
                    <Moon className="w-4 h-4 sm:w-5 sm:h-5" />
                  )}
                </motion.div>
              </motion.button>
            </div>
          </div>
        </div>
      </div>
    </motion.header>
  );
}
