import { create } from 'zustand';
import { persist } from 'zustand/middleware';
import type { 
  Decision, 
  Team, 
  MemoryFile, 
  SystemStatus, 
  Notification,
  Theme,
  DashboardStats,
  Agent
} from '../types';

interface DashboardState {
  // Theme
  theme: Theme;
  toggleTheme: () => void;
  setTheme: (theme: Theme) => void;
  
  // Decisions
  decisions: Decision[];
  addDecision: (decision: Omit<Decision, 'id' | 'createdAt' | 'status'>) => void;
  approveDecision: (id: string) => void;
  rejectDecision: (id: string) => void;
  updateDecisionAgent: (id: string, agentName: string) => void;
  
  // Teams & Agents
  teams: Team[];
  updateAgentStatus: (teamId: string, agentId: string, status: Agent['status']) => void;
  
  // Memory Files
  memoryFiles: MemoryFile[];
  setSelectedMemory: (id: string | null) => void;
  selectedMemory: string | null;
  searchMemory: (query: string) => MemoryFile[];
  
  // System Status
  systemStatus: SystemStatus;
  setSystemStatus: (status: Partial<SystemStatus>) => void;
  
  // Notifications
  notifications: Notification[];
  addNotification: (notification: Omit<Notification, 'id' | 'timestamp'>) => void;
  dismissNotification: (id: string) => void;
  
  // Stats
  stats: DashboardStats;
  updateStats: () => void;
}

export const useDashboardStore = create<DashboardState>()(
  persist(
    (set, get) => ({
      // Theme
      theme: 'dark',
      toggleTheme: () => set((state) => ({ 
        theme: state.theme === 'dark' ? 'light' : 'dark' 
      })),
      setTheme: (theme) => set({ theme }),
      
      // Decisions
      decisions: [
        {
          id: 'dec-001',
          title: 'Hamilton Library Application',
          description: 'Submit tailored resume for Librarian position',
          context: 'URGENT: Due Feb 27 - Hamilton Public Library',
          priority: 'urgent',
          status: 'pending',
          createdAt: new Date().toISOString(),
        },
        {
          id: 'dec-002',
          title: 'Firebird Grant Approval',
          description: 'Review and submit OTF grant application',
          context: 'Due March 15 - Ontario Trillium Foundation',
          priority: 'high',
          status: 'pending',
          createdAt: new Date().toISOString(),
        },
        {
          id: 'dec-003',
          title: 'OPLA Meeting Schedule',
          description: 'Book 2-hour strategy review session',
          context: '147 survey responses ready for analysis',
          priority: 'medium',
          status: 'pending',
          createdAt: new Date().toISOString(),
        },
      ],
      addDecision: (decision) => set((state) => ({
        decisions: [{
          ...decision,
          id: `dec-${Date.now()}`,
          status: 'pending',
          createdAt: new Date().toISOString(),
        }, ...state.decisions],
      })),
      approveDecision: (id) => set((state) => ({
        decisions: state.decisions.map((d) =>
          d.id === id ? { ...d, status: 'approved' } : d
        ),
      })),
      rejectDecision: (id) => set((state) => ({
        decisions: state.decisions.map((d) =>
          d.id === id ? { ...d, status: 'rejected' } : d
        ),
      })),
      updateDecisionAgent: (id, agentName) => set((state) => ({
        decisions: state.decisions.map((d) =>
          d.id === id ? { ...d, spawnedAgent: agentName } : d
        ),
      })),
      
      // Teams
      teams: [
        {
          id: 'marketing',
          name: 'Marketing',
          icon: '',
          agents: [
            { id: 'voice', name: 'The Voice', role: 'Social Media', status: 'active' },
            { id: 'scribe', name: 'The Scribe', role: 'Content Writer', status: 'active' },
          ],
        },
        {
          id: 'product',
          name: 'Product',
          icon: '',
          agents: [
            { id: 'tailor', name: 'The Tailor', role: 'Resume Builder', status: 'active' },
            { id: 'curator', name: 'The Curator', role: 'GitHub Manager', status: 'standby' },
          ],
        },
        {
          id: 'research',
          name: 'Research',
          icon: '',
          agents: [
            { id: 'mechanic', name: 'The Mechanic', role: 'Grant Writer', status: 'active' },
            { id: 'analyst', name: 'The Analyst', role: 'Survey Analysis', status: 'active' },
          ],
        },
        {
          id: 'utility',
          name: 'Utility',
          icon: '',
          agents: [
            { id: 'nightwatch', name: 'Night Watch', role: 'Automation', status: 'active' },
            { id: 'navigator', name: 'Navigator', role: 'PR Tracker', status: 'planned' },
          ],
        },
      ],
      updateAgentStatus: (teamId, agentId, status) => set((state) => ({
        teams: state.teams.map((team) =>
          team.id === teamId
            ? {
                ...team,
                agents: team.agents.map((agent) =>
                  agent.id === agentId ? { ...agent, status } : agent
                ),
              }
            : team
        ),
      })),
      
      // Memory Files
      memoryFiles: [
        {
          id: 'mem-001',
          name: 'USER.md',
          preview: 'TJ  Barrie, ON  Night owl  PR #1 priority',
          content: '## User Profile\n\n**Name:** TJ\n**Location:** Barrie, Ontario, Canada\n**Time Zone:** Eastern Time (Toronto)\n\n### Characteristics\n- Night owl productivity pattern\n- Prefers deep work sessions\n- Values automation and efficiency\n\n### Current Priorities\n1. Permanent Residency application\n2. Full-time employment in library/archives field\n3. Master\'s degree in Information Science',
          lastModified: '2025-02-17',
          category: 'user',
        },
        {
          id: 'mem-002',
          name: 'MEMORY.md',
          preview: '90-day goals: PR, Job, Masters',
          content: '## 90-Day Mission: Stability\n\n### Goals\n1. **Permanent Residency** - Submit complete application\n2. **Full-time Job** - Secure library/archives position\n3. **Master\'s Program** - Apply to UofT MI program\n\n### Key Deadlines\n- Feb 27: Hamilton Library application\n- Mar 15: Firebird grant deadline\n- Apr 30: PR document collection complete',
          lastModified: '2025-02-17',
          category: 'memory',
        },
        {
          id: 'mem-003',
          name: '2025-02-17.md',
          preview: 'Pleiades OS dashboard deployed',
          content: '## Daily Log - February 17, 2025\n\n### Accomplishments\n- Deployed Pleiades OS v2.0 with React\n- Implemented real-time WebSocket updates\n- Added interactive decision workflow\n\n### Active Projects\n- Hamilton Library application (urgent)\n- Firebird grant review\n- OPLA survey analysis',
          lastModified: '2025-02-17',
          category: 'daily',
        },
        {
          id: 'mem-004',
          name: 'AGENTS.md',
          preview: 'AI Team structure and responsibilities',
          content: '## AI Agent Organization\n\n### Marketing Team\n- The Voice: Social media management\n- The Scribe: Content creation\n\n### Product Team\n- The Tailor: Resume customization\n- The Curator: Portfolio management\n\n### Research Team\n- The Mechanic: Grant writing\n- The Analyst: Data analysis\n\n### Utility Team\n- Night Watch: System automation\n- Navigator: Immigration tracking',
          lastModified: '2025-02-17',
          category: 'project',
        },
        {
          id: 'mem-005',
          name: 'JOBS.md',
          preview: 'Active job applications tracking',
          content: '## Active Applications\n\n### Hamilton Public Library\n- Position: Librarian I\n- Status: Application due Feb 27\n- Priority: URGENT\n\n### Toronto Public Library\n- Position: Library Assistant\n- Status: Applied, awaiting response\n\n### University of Toronto\n- Position: Archives Assistant\n- Status: Phone interview scheduled',
          lastModified: '2025-02-16',
          category: 'project',
        },
      ],
      selectedMemory: null,
      setSelectedMemory: (id) => set({ selectedMemory: id }),
      searchMemory: (query) => {
        const { memoryFiles } = get();
        if (!query.trim()) return memoryFiles;
        const lowerQuery = query.toLowerCase();
        return memoryFiles.filter(
          (file) =>
            file.name.toLowerCase().includes(lowerQuery) ||
            file.preview.toLowerCase().includes(lowerQuery) ||
            file.content?.toLowerCase().includes(lowerQuery)
        );
      },
      
      // System Status - Static mode by default for GitHub Pages deployment
      systemStatus: {
        gateway: 'active',
        websocket: 'static',
        lastSync: new Date().toISOString(),
      },
      setSystemStatus: (status) => set((state) => ({
        systemStatus: { ...state.systemStatus, ...status },
      })),
      
      // Notifications
      notifications: [],
      addNotification: (notification) => set((state) => ({
        notifications: [
          {
            ...notification,
            id: `notif-${Date.now()}`,
            timestamp: new Date().toISOString(),
          },
          ...state.notifications.slice(0, 9), // Keep last 10
        ],
      })),
      dismissNotification: (id) => set((state) => ({
        notifications: state.notifications.filter((n) => n.id !== id),
      })),
      
      // Stats
      stats: {
        activeDecisions: 3,
        pendingApprovals: 3,
        activeAgents: 6,
        memoryFiles: 5,
      },
      updateStats: () => set((state) => ({
        stats: {
          activeDecisions: state.decisions.filter((d) => d.status === 'pending').length,
          pendingApprovals: state.decisions.filter((d) => d.status === 'pending').length,
          activeAgents: state.teams.flatMap((t) => t.agents).filter((a) => a.status === 'active').length,
          memoryFiles: state.memoryFiles.length,
        },
      })),
    }),
    {
      name: 'pleiades-os-storage',
      partialize: (state) => ({ 
        theme: state.theme,
        decisions: state.decisions,
      }),
    }
  )
);

// Selector hooks for performance
export const useTheme = () => useDashboardStore((state) => state.theme);
export const useDecisions = () => useDashboardStore((state) => state.decisions);
export const useTeams = () => useDashboardStore((state) => state.teams);
export const useMemoryFiles = () => useDashboardStore((state) => state.memoryFiles);
export const useSystemStatus = () => useDashboardStore((state) => state.systemStatus);
export const useStats = () => useDashboardStore((state) => state.stats);