function formatDuration(startTime: string): string {
  const start = new Date(startTime).getTime();
  const now = Date.now();
  const diff = now - start;
  const minutes = Math.floor(diff / (1000 * 60));
  
  if (minutes < 1) return 'Just now';
  if (minutes < 60) return `${minutes}m`;
  const hours = Math.floor(minutes / 60);
  const remainingMinutes = minutes % 60;
  return `${hours}h ${remainingMinutes}m`;
}

function SessionCard({ session }: { session: ActiveSession }) {
  const statusConfig = {
    reasoning: { 
      icon: Brain, 
      color: 'text-violet-500', 
      bg: 'bg-violet-500/10',
      label: 'Reasoning'
    },
    executing: { 
      icon: Zap, 
      color: 'text-emerald-500', 
      bg: 'bg-emerald-500/10',
      label: 'Executing'
    },
    waiting: { 
      icon: Clock, 
      color: 'text-amber-500', 
      bg: 'bg-amber-500/10',
      label: 'Waiting'
    },
    complete: { 
      icon: CheckCircle2, 
      color: 'text-slate-500', 
      bg: 'bg-slate-500/10',
      label: 'Complete'
    },
  };

  const modelColor = session.model === 'kimi-k2.5' 
    ? 'text-violet-500 bg-violet-500/10' 
    : session.model === 'gemini-2.5-flash'
    ? 'text-blue-500 bg-blue-500/10'
    : 'text-amber-500 bg-amber-500/10';

  const config = statusConfig[session.status];
  const StatusIcon = config.icon;

  return (
    <motion.div
      layout
      initial={{ opacity: 0, x: -10 }}
      animate={{ opacity: 1, x: 0 }}
      whileHover={{ scale: 1.01 }}
      className={cn(
        'relative rounded-xl p-3 border transition-all',
        'bg-slate-50/50 dark:bg-slate-800/30',
        'border-slate-200 dark:border-slate-700',
        'hover:border-slate-300 dark:hover:border-slate-600',
        'hover:shadow-md'
      )}
    >
      <div className="flex items-start gap-3">
        <motion.div 
          animate={session.status === 'reasoning' ? { 
            scale: [1, 1.1, 1],
            opacity: [1, 0.7, 1]
          } : {}}
          transition={{ duration: 2, repeat: Infinity }}
          className={cn('w-8 h-8 rounded-lg flex items-center justify-center', config.bg, config.color)}
        >
          <StatusIcon className="w-4 h-4" />
        </motion.div>
        
        <div className="flex-1 min-w-0">
          <div className="flex items-center gap-2">
            <h4 className="font-medium text-slate-900 dark:text-white text-sm">
              {session.agent}
            </h4>
            <span className={cn('px-1.5 py-0.5 rounded text-[10px] font-medium', modelColor)}>
              {session.model === 'kimi-k2.5' ? 'Kimi' : 'Gemini'}
            </span>
          </div>
          <p className="text-xs text-slate-500 dark:text-slate-400 mt-0.5 truncate">
            {session.task}
          </p>
          
          <div className="flex items-center gap-3 mt-2 text-[10px] text-slate-400">
            <span className="flex items-center gap-1">
              <Clock className="w-3 h-3" />
              {formatDuration(session.startTime)}
            </span>
            <span className="flex items-center gap-1">
              <Hash className="w-3 h-3" />
              {(session.tokensUsed / 1000).toFixed(1)}k tokens
            </span>
            <span className="flex items-center gap-1">
              <DollarSign className="w-3 h-3" />
              ${session.cost.toFixed(3)}
            </span>
          </div>
        </div>
      </div>
    </motion.div>
  );
}

function EngineCard({ 
  name, 
  status, 
  load, 
  requests, 
  isPrimary 
}: { 
  name: string; 
  status: string; 
  load: number; 
  requests: number;
  isPrimary: boolean;
}) {
  const statusColors = {
    active: 'text-emerald-500',
    standby: 'text-blue-500',
    degraded: 'text-amber-500',
    offline: 'text-rose-500',
  };

  const loadColor = load > 80 ? 'bg-rose-500' : load > 50 ? 'bg-amber-500' : 'bg-emerald-500';

  return (
    <div className={cn(
      'relative rounded-xl p-4 border transition-all',
      'bg-slate-50/50 dark:bg-slate-800/30',
      'border-slate-200 dark:border-slate-700'
    )}>
      <div className="flex items-center justify-between mb-3">
        <div className="flex items-center gap-2">
          <div className={cn(
            'w-8 h-8 rounded-lg flex items-center justify-center',
            isPrimary ? 'bg-violet-500/10 text-violet-500' : 'bg-blue-500/10 text-blue-500'
          )}>
            {isPrimary ? <Brain className="w-4 h-4" /> : <Zap className="w-4 h-4" />}
          </div>
          <div>
            <h4 className="font-medium text-slate-900 dark:text-white text-sm">
              {name}
            </h4>
            <p className="text-[10px] text-slate-400">
              {isPrimary ? 'Primary Engine' : 'Fallback'}
            </p>
          </div>
        </div>
        
        <div className="flex items-center gap-1.5">
          {status === 'active' ? (
            <Wifi className="w-3.5 h-3.5 text-emerald-500" />
          ) : status === 'offline' ? (
            <WifiOff className="w-3.5 h-3.5 text-rose-500" />
          ) : (
            <Activity className={cn('w-3.5 h-3.5', statusColors[status as keyof typeof statusColors])} />
          )}
          <span className={cn('text-xs font-medium capitalize', statusColors[status as keyof typeof statusColors])}>
            {status}
          </span>
        </div>
      </div>

      {/* Load bar */}
      <div className="space-y-2">
        <div className="flex items-center justify-between text-xs">
          <span className="text-slate-500">Load</span>
          <span className="font-medium text-slate-700 dark:text-slate-300">{load}%</span>
        </div>
        <div className="h-2 rounded-full bg-slate-200 dark:bg-slate-700 overflow-hidden">
          <motion.div
            initial={{ width: 0 }}
            animate={{ width: `${load}%` }}
            transition={{ duration: 1, ease: 'easeOut' }}
            className={cn('h-full rounded-full', loadColor)}
          />
        </div>
      </div>

      {/* Stats */}
      <div className="flex items-center gap-4 mt-3 pt-3 border-t border-slate-200 dark:border-slate-700">
        <div className="flex items-center gap-1.5 text-xs text-slate-500">
          <Terminal className="w-3.5 h-3.5" />
          <span>{requests} req</span>
        </div>
      </div>
    </div>
  );
}

