export function MissionControl({ 
  sessions: initialSessions, 
  routing: initialRouting 
}: MissionControlProps) {
  const [sessions, setSessions] = useState<ActiveSession[]>(initialSessions || defaultSessions);
  const [routing, setRouting] = useState<EngineRouting>(initialRouting || defaultRouting);
  const [loading, setLoading] = useState(false);
  
  const API_URL = import.meta.env.VITE_API_URL || 'http://localhost:3001';
  
  // Fetch real data on mount
  useEffect(() => {
    fetchData();
    // Refresh every 30 seconds
    const interval = setInterval(fetchData, 30000);
    return () => clearInterval(interval);
  }, []);
  
  async function fetchData() {
    setLoading(true);
    try {
      // Fetch sessions
      const sessionsRes = await fetch(`${API_URL}/api/sessions`);
      if (sessionsRes.ok) {
        const sessionsData = await sessionsRes.json();
        setSessions(sessionsData);
      }
      
      // Fetch routing
      const routingRes = await fetch(`${API_URL}/api/routing`);
      if (routingRes.ok) {
        const routingData = await routingRes.json();
        setRouting(routingData);
      }
    } catch (error) {
      console.error('Failed to fetch mission control data:', error);
    } finally {
      setLoading(false);
    }
  }
  
  const activeSessions = sessions.filter(s => s.status !== 'complete');
  const totalTokens = sessions.reduce((acc, s) => acc + s.tokensUsed, 0);
  const totalCost = sessions.reduce((acc, s) => acc + s.cost, 0);

  return (
    <motion.section
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.6, duration: 0.5 }}
      className="glass-panel p-4 sm:p-6"
    >
      {/* Header */}
      <div className="flex items-center justify-between gap-3 mb-5">
        <div className="flex items-center gap-3">
          <div className="w-10 h-10 rounded-xl bg-gradient-to-br from-rose-500/20 to-orange-500/20 flex items-center justify-center">
            <Radio className="w-5 h-5 text-rose-500" />
          </div>
          <div>
            <h2 className="text-lg sm:text-xl font-bold text-slate-900 dark:text-white font-display">
              Mission Control
            </h2>
            <p className="text-xs text-slate-500 dark:text-slate-400">
              {activeSessions.length} active sessions  {(totalTokens / 1000).toFixed(1)}k tokens
            </p>
          </div>
        </div>

        <div className="flex items-center gap-2">
          <motion.button
            whileHover={{ scale: 1.05 }}
            whileTap={{ scale: 0.95 }}
            onClick={fetchData}
            disabled={loading}
            className="p-2 rounded-lg bg-slate-100 dark:bg-slate-800 text-slate-500 hover:text-slate-700 dark:hover:text-slate-300 transition-colors"
          >
            <RefreshCw className={cn("w-4 h-4", loading && "animate-spin")} />
          </motion.button>
          
          <div className="text-right hidden sm:block">
            <p className="text-[10px] text-slate-400">Session Cost</p>
            <p className="text-sm font-medium text-slate-700 dark:text-slate-300">
              ${totalCost.toFixed(3)}
            </p>
          </div>
        </div>
      </div>

      {/* Engine Routing Cards */}
      <div className="grid grid-cols-3 gap-3 mb-4">
        <EngineCard
          name={routing.primary.name}
          status={routing.primary.status}
          load={routing.primary.load}
          requests={routing.primary.requests}
          isPrimary={true}
        />
        <EngineCard
          name={routing.fallback.name}
          status={routing.fallback.status}
          load={routing.fallback.load}
          requests={routing.fallback.requests}
          isPrimary={false}
        />
        <OpenRouterCard />
      </div>

      {/* View Toggle */}
      <div className="flex items-center justify-between mb-3">
        <h3 className="text-sm font-semibold text-slate-900 dark:text-white flex items-center gap-2">
          <Layers className="w-4 h-4 text-slate-400" />
          Active Sessions
        </h3>
        <span className="text-xs text-slate-400">
          {activeSessions.length} running
        </span>
      </div>

      {/* Sessions List */}
      <div className="space-y-2 max-h-[240px] overflow-y-auto scrollbar-premium pr-1">
        {loading ? (
          <div className="flex items-center justify-center py-8">
            <Loader2 className="w-6 h-6 text-rose-500 animate-spin" />
          </div>
        ) : activeSessions.length > 0 ? (
          activeSessions.map((session) => (
            <SessionCard key={session.id} session={session} />
          ))
        ) : (
          <div className="text-center py-8">
            <div className="w-12 h-12 rounded-xl bg-slate-100 dark:bg-slate-800 flex items-center justify-center mx-auto mb-3">
              <Cpu className="w-6 h-6 text-slate-400" />
            </div>
            <p className="text-sm text-slate-500">No active sessions</p>
          </div>
        )}
      </div>
    </motion.section>
  );
}
