import { useEffect } from 'react';
import { motion } from 'framer-motion';
import { Sparkles, Heart, Github } from 'lucide-react';
import { useWebSocket } from '../hooks/useWebSocket';
import { useThemeEffect } from '../hooks/useThemeEffect';
import { useDashboardStore } from '../store';
import { Header } from './Header';
import { MissionBanner } from './MissionBanner';
import { MorningReport } from './MorningReport';
import { DecisionPanel } from './DecisionPanel';
import { BrainPanel } from './BrainPanel';
import { AgentOrgChart } from './AgentOrgChart';
import { StarBackground } from './StarBackground';
import { NotificationCenter } from './NotificationCenter';
import { AutomationsPanel } from './AutomationsPanel';
import { MissionControl } from './MissionControl';

export function App() {
  // Initialize WebSocket connection (only if backend URL is configured)
  // For GitHub Pages static deployment, VITE_WS_URL won't be set
  const wsUrl = import.meta.env.VITE_WS_URL;
  useWebSocket(wsUrl);
  
  // Apply theme class to document
  useThemeEffect();
  
  // Update stats on mount
  const { updateStats } = useDashboardStore();
  
  useEffect(() => {
    updateStats();
  }, [updateStats]);

  return (
    <div className="relative min-h-screen overflow-x-hidden bg-light-gradient dark:bg-dark-gradient text-slate-900 dark:text-slate-100 transition-colors duration-300">
      {/* Animated Star Background */}
      <StarBackground />
      
      {/* Notification Center */}
      <NotificationCenter />
      
      {/* Header */}
      <Header />
      
      {/* Main Content */}
      <main className="relative z-10 max-w-7xl mx-auto px-3 sm:px-4 lg:px-6 py-4 sm:py-6 pb-24 sm:pb-6">
        {/* Mission Banner */}
        <section className="mb-4 sm:mb-6">
          <MissionBanner />
        </section>
        
        {/* Morning Report - Full Width */}
        <section className="mb-4 sm:mb-6">
          <MorningReport />
        </section>
        
        {/* Two Column Layout: Decisions & Brain */}
        <section className="grid grid-cols-1 lg:grid-cols-5 gap-4 sm:gap-6 mb-4 sm:mb-6">
          {/* Decisions Panel - Takes 2 columns on large screens */}
          <div className="lg:col-span-2">
            <DecisionPanel />
          </div>
          
          {/* Brain Panel - Takes 3 columns on large screens */}
          <div className="lg:col-span-3">
            <BrainPanel />
          </div>
        </section>
        
        {/* Agent Organization Chart - Full Width */}
        <section className="mb-4 sm:mb-6">
          <AgentOrgChart />
        </section>
        
        {/* Two Column Layout: Automations & Mission Control */}
        <section className="grid grid-cols-1 lg:grid-cols-2 gap-4 sm:gap-6 mb-4 sm:mb-6">
          <AutomationsPanel />
          <MissionControl />
        </section>
      </main>
      
      {/* Footer */}
      <motion.footer
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ delay: 1 }}
        className="relative z-10 max-w-7xl mx-auto px-4 py-6 text-center"
      >
        <div className="glass-panel p-4 sm:p-6 inline-block">
          <div className="flex flex-col sm:flex-row items-center justify-center gap-3 text-sm text-slate-500 dark:text-slate-400">
            <div className="flex items-center gap-2">
              <Sparkles className="w-4 h-4 text-violet-500" />
              <span className="font-medium">Pleiades OS</span>
            </div>
            <span className="hidden sm:inline text-slate-300 dark:text-slate-600"></span>
            <span>TJ's Command Center</span>
            <span className="hidden sm:inline text-slate-300 dark:text-slate-600"></span>
            <span>Eastern Time (Toronto)</span>
          </div>
          
          <div className="flex items-center justify-center gap-4 mt-3 text-xs text-slate-400 dark:text-slate-500">
            <span className="flex items-center gap-1">
              <Heart className="w-3 h-3 text-rose-500" />
              Made with care
            </span>
            <span className="flex items-center gap-1">
              <Github className="w-3 h-3" />
              Open Source
            </span>
          </div>
          
          <p className="text-[10px] text-slate-400 dark:text-slate-600 mt-3">
            React 18  TypeScript  Vite  Tailwind CSS  Framer Motion
          </p>
        </div>
      </motion.footer>
    </div>
  );
}
