import { useEffect, useRef, useCallback } from 'react';
import { io, Socket } from 'socket.io-client';
import { useDashboardStore } from '../store';
import type { Decision, Notification } from '../types';

interface WebSocketMessage {
  type: 'decision_update' | 'agent_spawned' | 'notification' | 'status_change' | 'sync';
  data: unknown;
  timestamp: string;
}

export function useWebSocket(url?: string) {
  const socketRef = useRef<Socket | null>(null);
  const { setSystemStatus, addNotification, updateDecisionAgent } = useDashboardStore();

  const connect = useCallback(() => {
    // If no WebSocket URL is provided, set to static mode and don't connect
    if (!url || url === 'ws://localhost:8080') {
      console.log('[WebSocket] No WebSocket URL configured, running in static mode');
      setSystemStatus({ 
        websocket: 'static',
        lastSync: new Date().toISOString()
      });
      return;
    }

    try {
      setSystemStatus({ websocket: 'connecting' });
      
      socketRef.current = io(url, {
        transports: ['websocket'],
        autoConnect: true,
        reconnection: true,
        reconnectionAttempts: 5,
        reconnectionDelay: 1000,
      });

      socketRef.current.on('connect', () => {
        setSystemStatus({ websocket: 'connected', lastSync: new Date().toISOString() });
        addNotification({
          title: 'Connected',
          message: 'Real-time updates enabled',
          type: 'success',
        });
      });

      socketRef.current.on('disconnect', () => {
        setSystemStatus({ websocket: 'disconnected' });
        addNotification({
          title: 'Disconnected',
          message: 'Real-time updates paused',
          type: 'warning',
        });
      });

      socketRef.current.on('message', (message: WebSocketMessage) => {
        handleMessage(message);
      });

      socketRef.current.on('decision_update', (data: Decision) => {
        addNotification({
          title: 'Decision Updated',
          message: `${data.title} status changed to ${data.status}`,
          type: 'info',
        });
      });

      socketRef.current.on('agent_spawned', (data: { decisionId: string; agentName: string }) => {
        updateDecisionAgent(data.decisionId, data.agentName);
        addNotification({
          title: 'Agent Spawned',
          message: `${data.agentName} is working on your request`,
          type: 'success',
        });
      });

      socketRef.current.on('notification', (data: Omit<Notification, 'id' | 'timestamp'>) => {
        addNotification(data);
      });
    } catch (error) {
      console.error('WebSocket connection error:', error);
      setSystemStatus({ websocket: 'disconnected' });
    }
  }, [url, setSystemStatus, addNotification, updateDecisionAgent]);

  const disconnect = useCallback(() => {
    socketRef.current?.disconnect();
  }, []);

  const sendMessage = useCallback((type: string, data: unknown) => {
    socketRef.current?.emit(type, {
      data,
      timestamp: new Date().toISOString(),
    });
  }, []);

  const handleMessage = useCallback((message: WebSocketMessage) => {
    console.log('WebSocket message received:', message);
  }, []);

  useEffect(() => {
    connect();
    return () => {
      disconnect();
    };
  }, [connect, disconnect]);

  return {
    socket: socketRef.current,
    connect,
    disconnect,
    sendMessage,
  };
}
