import { motion } from 'framer-motion';
import { TrendingUp, Clock, ChevronRight } from 'lucide-react';

interface MissionGoal {
  label: string;
  status: 'completed' | 'in-progress' | 'pending';
}

const missionGoals: MissionGoal[] = [
  { label: 'Permanent Residency', status: 'in-progress' },
  { label: 'Full-time Job', status: 'in-progress' },
  { label: 'Master\'s Program', status: 'pending' },
];

export function MissionBanner() {
  const completedGoals = missionGoals.filter(g => g.status === 'completed').length;
  const progress = (completedGoals / missionGoals.length) * 100;

  return (
    <motion.section
      initial={{ opacity: 0, y: 20 }}
      animate={{ opacity: 1, y: 0 }}
      transition={{ delay: 0.2, duration: 0.5 }}
      className="glass-panel p-4 sm:p-6 overflow-hidden relative"
    >
      {/* Background gradient accent */}
      <div className="absolute top-0 right-0 w-64 h-64 bg-gradient-to-br from-violet-500/10 to-fuchsia-500/10 rounded-full blur-3xl -translate-y-1/2 translate-x-1/2" />
      
      <div className="relative">
        {/* Header row */}
        <div className="flex flex-col sm:flex-row sm:items-center justify-between gap-4 mb-4">
          <div className="flex items-start sm:items-center gap-3 sm:gap-4">
            {/* Animated mission icon */}
            <motion.div
              animate={{ 
                rotate: [0, 5, -5, 0],
                scale: [1, 1.05, 1],
              }}
              transition={{ duration: 4, repeat: Infinity, repeatDelay: 3 }}
              className="w-12 h-12 sm:w-14 sm:h-14 rounded-xl sm:rounded-2xl bg-gradient-to-br from-violet-500 to-fuchsia-500 flex items-center justify-center text-xl sm:text-2xl shadow-lg shadow-violet-500/30 flex-shrink-0"
            >
              
            </motion.div>
            
            <div>
              <div className="flex items-center gap-2 mb-1">
                <span className="px-2 py-0.5 rounded-full text-[10px] font-semibold bg-violet-500/20 text-violet-700 dark:text-violet-300 border border-violet-500/30">
                  90-DAY MISSION
                </span>
                <motion.span
                  animate={{ opacity: [0.5, 1, 0.5] }}
                  transition={{ duration: 2, repeat: Infinity }}
                  className="flex items-center gap-1 text-[10px] text-emerald-500 font-medium"
                >
                  <span className="w-1.5 h-1.5 rounded-full bg-emerald-500 animate-pulse" />
                  ACTIVE
                </motion.span>
              </div>
              <h2 className="text-lg sm:text-xl font-bold text-slate-900 dark:text-white font-display">
                Stability
              </h2>
              <p className="text-xs sm:text-sm text-slate-500 dark:text-slate-400 mt-0.5">
                Building a foundation for the future
              </p>
            </div>
          </div>

          {/* Progress indicator */}
          <div className="flex items-center gap-3 bg-slate-50 dark:bg-slate-800/50 rounded-xl px-3 py-2 border border-slate-200 dark:border-slate-700">
            <div className="text-right">
              <div className="text-lg font-bold text-slate-900 dark:text-white tabular-nums">
                {completedGoals}<span className="text-slate-400">/{missionGoals.length}</span>
              </div>
              <div className="text-[10px] text-slate-500 dark:text-slate-400 uppercase tracking-wide">
                Objectives
              </div>
            </div>
            <div className="w-12 h-12 relative">
              <svg className="w-12 h-12 transform -rotate-90">
                <circle
                  cx="24"
                  cy="24"
                  r="20"
                  stroke="currentColor"
                  strokeWidth="4"
                  fill="none"
                  className="text-slate-200 dark:text-slate-700"
                />
                <motion.circle
                  initial={{ pathLength: 0 }}
                  animate={{ pathLength: progress / 100 }}
                  transition={{ duration: 1, delay: 0.5 }}
                  cx="24"
                  cy="24"
                  r="20"
                  stroke="url(#gradient)"
                  strokeWidth="4"
                  fill="none"
                  strokeLinecap="round"
                  className="drop-shadow-lg"
                />
                <defs>
                  <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="0%">
                    <stop offset="0%" stopColor="#8b5cf6" />
                    <stop offset="100%" stopColor="#ec4899" />
                  </linearGradient>
                </defs>
              </svg>
              <div className="absolute inset-0 flex items-center justify-center">
                <TrendingUp className="w-4 h-4 text-violet-500" />
              </div>
            </div>
          </div>
        </div>

        {/* Goals roadmap */}
        <div className="grid grid-cols-1 sm:grid-cols-3 gap-3">
          {missionGoals.map((goal, index) => (
            <motion.div
              key={goal.label}
              initial={{ opacity: 0, x: -10 }}
              animate={{ opacity: 1, x: 0 }}
              transition={{ delay: 0.3 + index * 0.1 }}
              className={`
                relative flex items-center gap-3 p-3 rounded-xl border transition-all duration-300
                ${goal.status === 'completed' 
                  ? 'bg-emerald-500/10 border-emerald-500/30' 
                  : goal.status === 'in-progress'
                    ? 'bg-violet-500/10 border-violet-500/30'
                    : 'bg-slate-50 dark:bg-slate-800/30 border-slate-200 dark:border-slate-700'
                }
              `}
            >
              {/* Status indicator */}
              <div className={`
                w-8 h-8 rounded-lg flex items-center justify-center flex-shrink-0
                ${goal.status === 'completed' 
                  ? 'bg-emerald-500 text-white' 
                  : goal.status === 'in-progress'
                    ? 'bg-violet-500 text-white'
                    : 'bg-slate-200 dark:bg-slate-700 text-slate-400'
                }
              `}>
                {goal.status === 'completed' ? (
                  <motion.svg 
                    initial={{ scale: 0 }} 
                    animate={{ scale: 1 }} 
                    className="w-4 h-4" 
                    fill="none" 
                    viewBox="0 0 24 24" 
                    stroke="currentColor"
                  >
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={3} d="M5 13l4 4L19 7" />
                  </motion.svg>
                ) : goal.status === 'in-progress' ? (
                  <Clock className="w-4 h-4" />
                ) : (
                  <span className="text-xs font-bold">{index + 1}</span>
                )}
              </div>

              <div className="flex-1 min-w-0">
                <p className={`
                  text-sm font-medium truncate
                  ${goal.status === 'pending' ? 'text-slate-500 dark:text-slate-400' : 'text-slate-900 dark:text-white'}
                `}>
                  {goal.label}
                </p>
                <p className="text-[10px] text-slate-500 dark:text-slate-400 capitalize">
                  {goal.status.replace('-', ' ')}
                </p>
              </div>

              {/* Arrow connector (not on last item) */}
              {index < missionGoals.length - 1 && (
                <ChevronRight className="hidden sm:block w-4 h-4 text-slate-300 dark:text-slate-600 absolute -right-2" />
              )}
            </motion.div>
          ))}
        </div>
      </div>
    </motion.section>
  );
}
